import React from 'react';
import Layout from './Layout';
import PagesLayout from './PagesLayout';

interface RouteWrapperProps {
  children: React.ReactNode;
  useLayout?: boolean | 'main' | 'pages' | 'none';
  showHeader?: boolean;
  showFooter?: boolean;
}

/**
 *   
 * @param useLayout - 'main':  , 'pages':  , 'none'  false:  
 * @param showHeader - PagesLayout      (: true)
 * @param showFooter - PagesLayout      (: true)
 */
const RouteWrapper: React.FC<RouteWrapperProps> = ({ 
  children, 
  useLayout = 'main',
  showHeader = true,
  showFooter = true
}) => {
  //   (components  )
  if (useLayout === true || useLayout === 'main') {
    return <Layout>{children}</Layout>;
  }
  
  // pages   
  if (useLayout === 'pages') {
    return (
      <PagesLayout showHeader={showHeader} showFooter={showFooter}>
        {children}
      </PagesLayout>
    );
  }
  
  //  
  return <>{children}</>;
};

export default RouteWrapper;
